
.client_list{
    width: 100%;
    height: 200px;
    border-bottom: 1px solid #d5d5d5;
    position: relative;
}

.client_list_inner{
    max-width: 1000px;
    height: 100%;
    margin: 0 auto !important;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding-top: 70px;
}

.client_list_inner img{
    width: 180px;
    height: 72px;
}






